import {IconCategory} from '@here/hds-iconlibrary';
import React from 'react';
import {AccordionItem} from '@here/hds-accordion';

// eslint-disable-next-line no-new
new AccordionItem();

export interface HDSAccordionItemProps extends React.HTMLAttributes<HTMLDivElement> {
  expanded?: boolean;
  disabled?: boolean;
  headline?: string;
  icon?: string;
  iconCategory?: IconCategory;
}

const HDSAccordionItem = React.forwardRef(
  (
    {children, disabled, expanded, iconCategory, className, ...rest}: HDSAccordionItemProps,
    ref
  ) => (
    <hds-accordion-item
      ref={ref}
      {...(className && {class: className})}
      {...(disabled && {disabled})}
      {...(expanded && {expanded})}
      {...(iconCategory && {'icon-category': iconCategory})}
      {...rest}
    >
      {children}
    </hds-accordion-item>
  )
);

HDSAccordionItem.defaultProps = {
  disabled: false,
  headline: '',
  expanded: false,
  iconCategory: 'core-ui',
};

export default HDSAccordionItem;
